/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
	display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */

*[hidden] {
    display: none;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

#MusicPlayer {
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 0;
  width: 100%;
}
#media-player {
  background-color: #ffffff;
  color: #2f3640;
  padding: 16px;
  margin: 0;
}
#media-player ul {
  /* background-color: rgba(50, 50, 50, 0.1);
  border-radius: 6px;
  padding: 16px; */
}
#media-player ul li {
  display: none;
}
#media-player ul li a {
  display: block;
  color: #000000;
  padding: 8px;
  font-size: 0.8rem;
  text-decoration: none;
}
/* #media-player ul li:hover, */
#media-player ul li.is-active {
  font-weight: bold;
  background-color: #ffffff;
  display: block;
}
.media-player__controls {
  padding: 0.5rem;
  width: 100%;
  border-radius: 6px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
@media (max-width: 640px) {
.media-player__controls {
    padding: 0;
}
}
.media-player__controls a {
  color: #eeeeee;
}
.media-player__controls a:hover {
  color: rgba(0, 0, 0, 0.75);
}
.skip-previous-button {
  margin-right: 0.2rem;
}
.skip-next-button {
  margin-left: 0.2rem;
  margin-right: 0.8rem;
}
.media-player__play-pause-button {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.media-player__play-pause-button a {
  color: #00bc7e;
}
.media-player__play-pause-button i {
  width: 30px;
  font-size: 30px;
}
.media-player__playback-container {
  position: relative;
}
.media-player__playback-container input {
  width: 100%;
  position: absolute;
  top: -10px;
  left: -3px;
  opacity: 0;
}
.media-player__play-pause-button:hover a {
  color: #008e5c;
}
.media-player__volume-container {
  display: inline-block;
  text-align: center;
}
.media-player__volume-container input {
  -webkit-appearance: none;
  width: 80%;
  height: 5px;
  border-radius: 2px;
  background-color: #eeeeee;
  outline: none;
  position: relative;
  top: -3px;
}
.media-player__volume-container input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 6px;
  background: #00bc7e;
  cursor: pointer;
}
.media-player__volume-container input::-moz-range-thumb {
  width: 15px;
  height: 15px;
  border-radius: 6px;
  background: #00bc7e;
  cursor: pointer;
}
.media-player__playback-container {
  height: 5px;
  width: 100%;
  border-radius: 2px;
  top: 0;
  position: relative;
  background-color: #eeeeee;
}
.media-player__playback-container div {
  background-color: #00bc7e;
  border-radius: 6px;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
}
.media-player__playback-container .currentTime {
  position: absolute;
  top: -16px;
  left: 0;
  font-size: 12px;
}
.media-player__playback-container .duration {
  position: absolute;
  top: -16px;
  right: 0;
  font-size: 12px;
}

